.app {
    max-width: 1200px;
    width: 100%;
    margin: 100px auto;
    box-shadow: 0 0 5px #ccc;
    font-family: Roboto;
  }
  .details {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 30px 0;
  }
  .details .large-img {
    width: 500px;
    height: 380px;
    overflow: hidden;
    margin: 25px;
  }
  .large-img img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
  
  .details .box {
    max-width: 500px;
    min-width: 290px;
    margin: 25px;
  }
  .box .row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  .box .row h2 {
    text-transform: uppercase;
    letter-spacing: 2px;
  }
  .box .row span {
    color: crimson;
    font-weight: 700;
  }
  .box .colors button {
    width: 30px;
    height: 30px;
    border: 1px solid #333;
    outline: none;
    margin-right: 5px;
    cursor: pointer;
  }
  .box p {
    line-height: 1.5;
    margin: 15px 0;
  }
  .thumb {
    width: 100%;
    display: flex;
    cursor: pointer;
    margin: 10px 0;
  }
  .thumb img {
    width: 100px;
    height: 100%;
    display: block;
    object-fit: cover;
    border: 1px solid #ddd;
    margin-right: 5px;
    opacity: 0.7;
    border-radius: 5px;
  }
  .thumb img.active {
    opacity: 1;
    border: 1px solid lightseagreen;
  }
  .box .add-to-cart {
    background: #333;
    color: white;
    outline: none;
    border: none;
    cursor: pointer;
    padding: 10px 25px;
    margin-top: 15px;
    border-radius: 15px;
  }
  .add-to-cart:disabled {
    pointer-events: none;
  }
  
  .add-to-cart:hover {
    color: #fff;
    background-color: #1A1A1A;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
  }
  
  .add-to-cart:active {
    box-shadow: none;
    transform: translateY(0);
  }
  
  /*------------ Responsive --------------*/
  @media (max-width: 500px) {
    .app {
      margin: 50px auto;
    }
    .details {
      padding: 25px 0;
    }
    .large-img-wrapper {
      text-align: center;
    }
    .details .large-img {
      width: 90%;
      height: 90%;
      margin: 0;
    }
    .details .box {
      width: 90%;
      height: 90%;
      margin: 0;
    }
    .thumb img {
      width: 68px;
    }
  }
  