/* Sidebar container styling */
.sidebar {
  width: 18%;
  position: absolute;
  margin-top: 3%;
  z-index: 3;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  overflow-y: visible;
}

/* Scrollbar styling */
.sidebar::-webkit-scrollbar {
  width: 10px;
}

.sidebar::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 8px;
}

.sidebar::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #a0a0a0, #505050);
  border-radius: 8px;
  border: 2px solid #f1f1f1;
}

.sidebar::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #888, #333);
}

.sidebar {
  scrollbar-width: thin;
  scrollbar-color: #a0a0a0 #f1f1f1;
}

/* Sidebar logo container */
.logo-container {
  margin-bottom: 1rem;
  text-align: center;
}

.logo-container h1 {
  margin-top: 0;
  font-size: 2.5rem;
  color: #333;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

/* Media query for screens smaller than 400px */
@media (max-width: 500px) {
  .sidebar {
    width: 10%; /* Adjust the width to take less space */
    left: 10%;  /* Add space to the left */
    right: 20%; /* Add space to the right */
    margin-top: 2%; /* Adjust top margin */
  }
}
